//
// Card
// --------------------------------------------------

// Common
// -------------------------

.card {
	position: relative;
	margin-bottom: @card-gutter-width;
	background-color: @white;
	color: @text-color;
	border-radius: @card-radius;

	.clearfix();
	.box-shadow(@z-height-1);

	.btn-icon-toggle,
	.btn-flat {
		&:hover { 
			background-color: fade(@black, 7%);
		}
	}

	// When a scroller is needed, add a border-radius bottom to it.
	> .nano:last-child {
		border-radius: 0 0 @card-radius @card-radius;
	}

	&.no-shadow {
		.box-shadow(@z-height-0);
	}
}


// Card head
// -------------------------

.card-head {
	position: relative;
	line-height: @card-header - 4px;
	min-height: @card-header;
	vertical-align: middle;
	border-radius: @card-radius @card-radius 0 0;
	.clearfix();

	header {
		display: inline-block;
		padding: 11px @card-gutter-width;
		vertical-align: middle;
		line-height: 17px;
		font-size: @title-size;

		> h1, > h2, > h3, > h4, > h5, > h6 {
			vertical-align: middle;
			margin: 0;
			line-height: 1;
		}
		small {
			color: inherit;
			opacity: 0.6;
			font-size: 75%;
		}
	}
	> div {
		display: inline-block;
	}

	.tools {
		padding-right: @card-gutter-width-sm;
		float: right;

		form {
			margin-top: 7px;
			margin-bottom: 7px;
			margin-left: @card-gutter-width;
			line-height: normal;
			vertical-align: middle;
		}

		.btn-floating-action {
			margin-left: 8px;
		}
		
		> .btn-group { margin-right: 4px; }
		> .btn-group-xs { margin-right: 10px; }
		> .btn-group-sm { margin-right: 4px; }
		> .btn-group-lg { margin-right: 0; }

		&.pull-left {
			> .btn-group { margin-left: 4px; }
			> .btn-group-xs { margin-left: 10px; }
			> .btn-group-sm { margin-left: 4px; }
			> .btn-group-lg { margin-left: 0; }
		}
	}

	&.card-head-xs {
		line-height: @card-header-xs - 4;
		min-height: @card-header-xs;

		header {
			font-size: @subhead-size;
			padding: 8px @card-gutter-width;
		}
	}
	&.card-head-sm {
		line-height: @card-header-sm - 4;
		min-height: @card-header-sm;
		header {
			font-size: @subhead-size;
		}
	}
	&.card-head-lg {
		line-height: @card-header-lg - 4;
		min-height: @card-header-lg;
		header {
			font-size: @headline-size;
		}
	}
}

.modal .card-head .tools {
		position:absolute;
		right:5px;
		top:0;
	}

// Card body
// -------------------------

.card-body {
	padding: @card-gutter-width;
	position: relative;
	.clearfix();

	&:last-child {
		border-radius: 0 0 @card-radius @card-radius;
	}
	&.table-responsive {
		margin: 0;
	}
}

// Card actionbar
// -------------------------

.card-actionbar {
	padding-bottom: @card-gutter-width-sm / 2;
	position: relative;
	.clearfix();

	&:last-child {
		border-radius: 0 0 @card-radius @card-radius;
	}
}

.card-actionbar-row {
	padding: 6px @card-gutter-width-sm;
	text-align: right;
}

// Card tiles & Card masonry
// -------------------------
// Removes padding & margin

.card-tiles,
.card-type-blog-masonry {
	> .row {
		margin: 0;
		> [class^="col-"] {
			padding: 0;
		}
	}
}


// Card underline
// -------------------------

.card-underline {
	.card-head {
		border-bottom: 1px solid fade(@gray-light, 40);
	}
	.card-foot {
		border-top: 1px solid fade(@gray-light, 40);
	}
}


// Card bordered & outlined
// -------------------------

.card-bordered,
.card-outlined {
	border: 2px solid @card-default-border;
}


// Card variants
// -------------------------

.card-variant(style-default-dark,	@brand-default-dark);
.card-variant(style-default,		@brand-default);
.card-variant(style-default-light,	@brand-default-light);
.card-variant(style-default-bright,	@brand-default-bright);

.card-variant(style-gray-dark,		@gray-dark);
.card-variant(style-gray,			@gray);
.card-variant(style-gray-light,		@gray-light);
.card-variant(style-gray-bright,	@gray-light2);

.card-variant(style-primary,		@brand-primary);
.card-variant(style-primary-light,	@brand-primary-light);
.card-variant(style-primary-dark,	@brand-primary-dark);
.card-variant(style-primary-bright,	@brand-primary-bright);

.card-variant(style-accent,			@brand-accent);
.card-variant(style-accent-light,	@brand-accent-light);
.card-variant(style-accent-dark,	@brand-accent-dark);
.card-variant(style-accent-bright,	@brand-accent-bright);

.card-variant(style-success,		@brand-success);
.card-variant(style-warning,		@brand-warning);
.card-variant(style-danger,			@brand-danger);
.card-variant(style-info,			@brand-info);


// ЗАКАЗЧИК
.card-variant(style-z_formir,		@bge-formir);
.card-variant(style-z_inwork,		@bge-inwork);
.card-variant(style-a_zgree,		@bge-agree);
.card-variant(style-z_lp,			@bge-lp);
.card-variant(style-z_arch,			@bge-arch);

// ПОСТАВЩИК
.card-variant(style-p_newest,		@bge-formir);
.card-variant(style-p_podacha,		@bge-inwork);
.card-variant(style-p_agree,		@bge-agree);
.card-variant(style-p_lp,			@bge-lp);
.card-variant(style-p_arch,			@bge-arch);



.card.style-transparent,
.card .style-transparent { 
	background-color: transparent;
	.box-shadow(@z-height-0);
	color: inherit;
}

.style-login {
	background:rgba(255, 255, 255, 0.8);
}
.style-image {
	background-image: url('/imgs/basko_bg2.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	.saturate(200%);
}


// Card transparent
// -------------------------

.card-transparent { 
	background-color: transparent;
	.box-shadow(@z-height-0);
	color: inherit;
	
	.card-head {
		header,
		.tools {
			padding-left: 4px;
			padding-right: 4px;
		}
	}
	.card-body {
		.box-shadow(@z-height-1);
	}
}


// Outlined
// -------------------------

.card.card-outlined {
	background-color: @white;
	background-image: none;
	color: @text-color;

	.btn-icon-toggle,
	.btn-flat {
		&:hover { 
			background-color: fade(@black, 7%);
		}
	}
}


// ADD-ONS
// --------------------------------------------------

// Loader
// -------------------------
// Add an card overlay for the loader

.card-loader {
	display: block;
	position: absolute;
	left: 0; right: 0; top: 0; bottom: 0;
	background-color: @white;
	background-color: rgba(255,255,255,0.6);
	z-index: 999;
}


// Collapsed
// -------------------------
// Add animation to the '.btn-collapse'

.card {
	.btn-collapse {
		.transition(all .1s ease-out);
	}
}
.card-collapsed {
	.btn-collapse {
		.rotate(180deg);
		.transition(all .3s ease-in);
	}
}


// Prettyprint style inside a card
// -------------------------
.card .prettyprint {
	margin: 0;
	border-left: none;
	border-right: none;
	border-bottom: none;
}



// Card TYPES
// --------------------------------------------------

// Pricing
// -------------------------

.card-type-pricing {
	.list-unstyled {
		margin: 0;
		li {
			padding: 15px 30px;
			border-top: 1px solid @gray-lighter;
		}
		li:first-child {
			border-top: none;
		}
	}

	.price {
		h1, h2, h3, h4, h5, h6 {
			display: inline-block;
			margin: 0;
		}
	}
}


// Blog Masonry
// -------------------------

.card-type-blog-masonry {
	article .blog-image {
		img {
			width: 100%; 
		}
	}

	article .blog-text {
		position: relative;
	}
	article > div + div.blog-text:before {
		position: absolute;
		margin: auto;
		top: -20px; left: 0; right: 0;
		#triangle > .t(@white, 20px, 20px);
		z-index: 1;
	}
	article .blog-text:first-child:before {
		position: absolute;
		margin: auto;
		bottom: -20px; left: 0; right: 0;
		#triangle > .b(@white, 20px, 20px);
		z-index: 1;
	}
}

// No shadow
// ------------







// Flip-card
// -----------------



.card-container {
	border-radius: 4px;
	height: 420px;
	margin-bottom: 30px;
	width: 100%;
	&:not(.manual-flip) {
		hover {
			.card {
				transform: rotateY( 180deg );
			}
			.back {
				visibility: visible;
				z-index: 5;
			}
		}
	}
}
.card-container.hover.manual-flip {
	.card {
		transform: rotateY( 180deg );
	}
}
.card-container.static {
	&:hover {
		.card {
			transform: none;
		}
	}
}
.card-container.static.hover {
	.card {
		transform: none;
	}
}
.card {
	background: none repeat scroll 0 0 #FFFFFF;
	border-radius: 4px;
	color: #444444;
	position: relative;
	transform-style: preserve-3d;
	transition: transform .5s;
	.cover {
		border-radius: 4px 4px 0 0;
		height: 105px;
		overflow: hidden;
		img {
			width: 100%;
		}
	}
	.user {
		border-radius: 50%;
		display: block;
		height: 120px;
		margin: -55px auto 0;
		overflow: hidden;
		width: 120px;
		img {
			background: none repeat scroll 0 0 #FFFFFF;
			border: 4px solid #FFFFFF;
			width: 100%;
		}
	}
	.content {
		background-color: rgba(0, 0, 0, 0);
		box-shadow: none;
		padding: 10px 20px 20px;
		.main {
			min-height: 160px;
		}
	}
	.back {
		.content {
			.main {
				height: 215px;
			}
		}
	}
	.name {
		font-size: 22px;
		line-height: 28px;
		margin: 10px 0 0;
		text-align: center;
		text-transform: capitalize;
	}
	h5 {
		font-weight: 400;
		line-height: 20px;
		margin: 5px 0;
	}
	.profession {
		color: #999999;
		margin-bottom: 20px;
		text-align: center;
	}
	.footer {
		border-top: 1px solid #EEEEEE;
		color: #999999;
		margin: 30px 0 0;
		padding: 10px 0 0;
		text-align: center;
		.social-links {
			font-size: 18px;
			a {
				margin: 0 7px;
			}
		}
		.btn-simple {
			margin-top: -6px;
		}
	}
	.header {
		height: 90px;
		padding: 15px 20px;
	}
	.motto {
		border-bottom: 1px solid #EEEEEE;
		color: #999999;
		font-size: 14px;
		font-weight: 400;
		padding-bottom: 10px;
		text-align: center;
	}
	.stats-container {
		margin-top: 50px;
		width: 100%;
	}
	.stats {
		display: block;
		float: left;
		text-align: center;
		width: 33.333333%;
		&:first-child {
			border-right: 1px solid #EEEEEE;
		}
		&:last-child {
			border-left: 1px solid #EEEEEE;
		}
		h4 {
			font-weight: 300;
			margin-bottom: 5px;
		}
		p {
			color: #777777;
		}
	}
}
.front {
	backface-visibility: hidden;
	background-color: #ffffff;
	border-radius: 4px;
	box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.14);
	height: 420px;
	left: 0;
	position: absolute;
	top: 0;
	width: 100%;
	z-index: 2;
	z-index: 4;
}
.back {
	-ms-transition: all 0.2s cubic-bezier(.92,.01,.83,.67);
	backface-visibility: hidden;
	background-color: #ffffff;
	border-radius: 4px;
	box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.14);
	height: 420px;
	left: 0;
	position: absolute;
	top: 0;
	transform: rotateY( 180deg );
	visibility: hidden;
	width: 100%;
	z-index: 3;
	.btn-simple {
		bottom: 4px;
		left: 0;
		position: absolute;
	}
}









// Responsive classes
// --------------------------------------------------

// Small Devices
// -------------------------

@media (max-width: @screen-xs-max) {
	.card {
		margin-bottom: @card-gutter-width-sm;
	}

	.card-head {
		header {
			padding: 11px @card-gutter-width-sm;
		}
		&.card-head-xs {
			header {
				padding: 8px @card-gutter-width-sm;
			}
		}
	}

	.card-body {
		padding: @card-gutter-width-sm;
	}

	.card .force-padding {
		padding: @card-gutter-width-sm;
	}
	.card .small-padding {
		padding: (@card-gutter-width-sm/2);
	}
	.card .form-padding {
		padding: 0 @card-gutter-width-sm;
	}
	.card .no-side-padding {
		padding: @card-gutter-width-sm 0;
	}
}