//
// Profile
// --------------------------------------------------

.profile {	
	font-size:120%;
	.card-head.nav-tabs {
		h4 {
			//font:130% 'Roboto Condensed', sans-serif;
			font-weight: 700;
			margin-bottom:1em;
		}
		i{
		  font-size: 3em;
    	vertical-align: -65%;
		}
	}
	.card-body {
		min-height:600px;
	}
	dt {
		margin-right: (@line-height-computed / 2);
	}
	dd {
  	margin: 0 0 (@line-height-computed / 2);
  	padding-left: 175px;
	}
}



@media (max-width: @screen-sm-max) {

}

// Extra Small Devices Only
// -------------------------

@media (max-width: @screen-xs-max) {

.profile {	
	font-size:100%;

	.card-head {
		padding:0 5px;
		> li > a {
			line-height:20px;
			padding:3px 6px;
		}
		&.nav-tabs {
			h4 {
				font-size:100%;
			}
			.fa-lg{
			  font-size: 2em;
	    	vertical-align: -65%;
			}
		}
	}
	.card-body {
		min-height:600px;
		h1 {
			font-size:200%;
		}
		dt {
			
		}
		dd {
			padding:0;
		}
	}

}


}


