//
// Offcanvas
// --------------------------------------------------


// General styles
// -------------------------

.offcanvas {
	position: fixed;
	top: 0; bottom: 0;
	right: 0;
	z-index: @zindex-offcanvas;
	p {
		font-size:110%;
	}
}
body.offcanvas-expanded {
	overflow: hidden;
}
.offcanvas:first-child {
	left: 0;
	right: auto;
}

		.offcanvas .btn-icon-toggle {
			width:40px;
			.md {
				font-size:30px;
				line-height:39px;
			}
		}




// Pane
// -------------------------

.offcanvas-pane {
	position: absolute;
	top: 0; bottom: 0;
	min-width: 8px;
	//max-width: 100%;
	background-color: @offcanvas-bg;
	overflow: auto;
	.box-shadow(@z-height-0);
	.transition-vendor(transform, @offcanvas-speed-out @offcanvas-ease-out);
	
	&.active {
		display: block;
		.box-shadow(@z-height-3);
		.transition-vendor(transform, @offcanvas-speed-in @offcanvas-ease-in);
	}
}
.offcanvas:first-child {
	.offcanvas-pane {
		right: 0;
	}
}


// Offcanvas body
// -------------------------

.offcanvas-body {
	padding: 16px;
	margin-bottom:-84px;
}


// Offcanvas head
// -------------------------

.offcanvas-head {
	position: relative;
	padding: 4px 16px;
	.box-shadow(@z-height-panel-inverse);
	
	header {
		font-size: @font-size-h2;
		//font-size: 24px;
    line-height: 1;
    margin: .5em 0 0 0;
	}
	
	.offcanvas-tools {
		position: absolute;
		left: 4px; right: 4px;
		top: 4px;
	}
  .btn {
    .transition(transform 320ms ease-out); 
  }
  .btn:active {
    .transform(scale(.8));
  }

}

// Offcanvas foot
// -------------------------
.offcanvas-foot {
	&:extend(.force-padding, .stick-bottom-right);
	left:0;
	right:0;
	text-align:right;
	.box-shadow(@z-height-panel-inverse);

}


.offcanvas > .stick-top-right,
.offcanvas > .stick-bottom-right,
.offcanvas > .stick-top-left-right,
.offcanvas > .stick-bottom-left-right {
	right: 0;
	left: 0;
  text-align: right;
 	.gradient(rgba(255, 255, 255, .5), #ffffff);
  z-index:100;
}



//	List
// -------------------------

.offcanvas-body .list {

	}


	.send-invit  {
		position:relative;
		margin-bottom:2em;
		 .tile {
			.tile-text {
				span {
					padding-left:44px!important;
					padding-right: 50px;
				}
			&:after {
					content:'';
					position:absolute;
					top:0;
			    right: 65px;
			    width: 100px;
			    height: 50px;
					z-index:10;
					.horizontal-gradient(rgba(255, 255, 255, 0), #ffffff);
			}
			&:hover {
				background: @gray-lightest;
				//color: @white;
				&:after {
					.horizontal-gradient(rgba(255, 255, 255, 0), @gray-lightest)
				}
				}


				
			}
			.tile-icon {
					position:absolute;
					right:5%;
					z-index: 10;
				}
			}
	}	

	@media (max-width: @screen-xs-max){ 
			.send-invit .tile .tile-text span {
				font-size: 90%!important;
				padding-left:30px!important;
			}
	}


