// Notification & Toasts
#toast-container{

	.toast{
		opacity: 1;

		background-image: none !important;
		padding: 16px 24px;
		box-shadow: none;

		animation: 100ms pulse;

		.toast-title{
			margin-bottom: 10px;
		}
		.toast-message{
			line-height: 24px;
			.btn{
				position: absolute;
				right: 24px;
				top: 12px;
			}
		}

		&:hover{
			box-shadow: none;
		}

		&.toast-info{
			background-color: #323232;
		}
		&.toast-success{
			background-color: @brand-success;
		}
		&.toast-warning{
			background-color: @brand-warning;
		}
		&.toast-error{
			background-color: @brand-danger;
		}
	}

	.toast-close-button {
		position: absolute;
		right: 24px;
		top: 12px;
		text-shadow: none;
		&:hover,
		&:focus{
			color: #fff;
			opacity: .5;
			outline: none;
		}
	}

	&.toast-top-right {
		top: 20px;
		right: 20px;
	}
	&.toast-bottom-right{
		bottom: 20px;
		right: 20px;
	}
	&.toast-bottom-left{
		bottom: 20px;
		left: 106px;
	}
	&.toast-top-left {
		top: 20px;
		left: 106px;
	}

	&.toast-top-full-width,
	&.toast-bottom-full-width{
		left: 0;
		.toast{
			width: 100%;
			border-radius: 0;
		}
	}

}