// Zayavki nav
// -------------------------

.nav-zaya {
  position:fixed;
  > ul {
    margin-top:1em;
  }
  ul.nav {
    //&:extend (.nav-pills, .nav-stacked, .nav-icon);
  }
}

  .nav-zaya {
    opacity: 0;
    //.rotateX(-90deg);
    .translate(-50px, 0);
    .transition(all 0.5s cubic-bezier(.36,-0.64,.34,1.76));
  }
  body.loaded & .nav-zaya {
    opacity: 1;
    transform: none;
  }



// Zayavki list
// -------------------------

.list-zaya {
  margin-bottom: 20px !important;

  //Анимация при загрузке
  .each-anim(list-group-item);


  
  .list-group-item {
    position: relative;
    border:none;
    height:120px;
      @media (max-width: @screen-md-min) { height:100px; }
    overflow: hidden;

    a {
      position: absolute;
      top: 0; left: 0; right: 0; bottom: 0;
      padding: 5px 20px 20px 20px;
      line-height: 1.4;
     }

    &:after {
        content:'';
        position: absolute;
        background:@list-group-hover-bg;
        top: 0;
        left: 0;
        right: 0;
        bottom:0;
        opacity: 0;
        z-index: -1;
        -webkit-transform: scale(0.9);
        .transition(all 320ms ease-out);
    }

    .loaded &:hover:after {
      opacity: 1;
      -webkit-transform: none
    }
    .loaded &:active:after {
      .transform(scale(.95));  
    }
    a:hover,
    a:focus {
      text-decoration: none;
      color: @list-group-link-hover-color;
      //background-color: @list-group-hover-bg;
        &:before {
          .horizontal-gradient(rgba(255, 255, 255, 0), rgb(245, 245, 245, 1))
        }
      }
    a:hover {
      //box-shadow: 0 4px 12px rgba(51,51,51,.14);
      }
    a:active {
      //box-shadow: 0 4px 12px rgba(51,51,51,.14);
        .transform(scale(.95));
        .transition(transform 320ms ease-out);
      }

    a:first-child {
      border-top: none;
    }
    
    h2, h3, h4, h5, h6 {
      margin-top: 0;
      margin-bottom: 5px;
    }

    h4 {
      max-height:32px;
      overflow:hidden;
      position:relative;
      margin-right: 40px;
    }

    h5 {
      font-size:80%;
      display:inline-block;
      padding: (@card-gutter-width / 4);
      background:#f5f5f5;
      margin-left: -10px;
    }
    &:hover h5 {
      //background:#fff;
    }

    p {
      color: lighten(@text-color, 50%);
      margin-bottom: 0px;
      line-height: 1.3;
      white-space: nowrap;
      overflow: hidden;
    }

    .stick-top-left,
    .stick-bottom-right {
      display:none;
    }

    .zdate {
      .text-emphasis-variant(@gray-light);
      font-size:75%;
      padding: (@card-gutter-width / 4);
      position: absolute;
      top:5px;
      right:5px;
      span {
         @media (max-width: @screen-sm-min) { display:none; }       
        }
    }

    .divider {
      position:absolute;
      bottom: 10px;
      left: 10px;
      right: 10px;
      //width:100%;
      span {
        //margin-right:10px;
        width:130px;
        font-size: 85%;
        float:left;
        text-transform: lowercase;
      }
      .progress {
        margin:0;
        margin-top: 6px;
      }
      .progress-bar {
      }
    }


  } //.list-group-item



}

  .zaya_sell_{
    .list-zaya {
      h5{ display:none;}
      .zdate{
        position: relative;
        top:0;
        margin:0;
        padding:0 6px;
      }
    }
  }







// Modals
// -------------------------

.modal {
  
  &.zayavka .modal-dialog .modal-content .card{
    //min-height:500px;
    .card-body{
      min-height:480px;
    }

  }


}

//МОДАЛЬНОЕ ОКНО С ЗАЯВКОЙ
.modal .zayavka {
  h3 {
    margin-top:0px;
    font-size:150%;
  }
  .st {
    font-size:100%;
    font-weight:normal;
    margin:0;
    padding:0;
    li {
      display:inline-block;
      margin:0 1em .5em 0;
      white-space: nowrap;
    }
  }
}

.zaya-desc {
    padding: 0;
    position: relative;
    font-size:1.1em;
      .nav-tabs {
        position:absolute;
        top:0;
        left:0;
        z-index:1200;
      }
      .nav-tabs > li > a {
        @media (max-width: @screen-sm-max) { 
          font-size: 90%;
          padding: 10px 5px;
        }
      }     

      .tab-pane {
         margin-top: 40px;
         //margin-top: 54px;
         min-height: 560px;
        @media (max-width: @screen-sm-max) { 
          min-height:460px;
        }
        &.tab-datatable {
          margin-top:0;
        }
      }

      #suppl .tab-pane {
        margin-top:0;
      }
      .tab-content {
        //padding:12px;
        padding:12px 0;
      }
      .dl-horizontal {
        padding:2em 0 0 0;
        dt {
          //width:100px;
        }
         dd {
        //margin-left:120px;
        }       

      }


      // Docs list
      // -------------------------

      .docs {
        margin-bottom: @grid-gutter-width;
        margin-top: 1px;
        //border-left: 1px solid @gray-lightest;
        .clearfix();
        .clearfix {
          margin:0;

        }

        > div {
          padding: 5px 10px;
          background: inherit;
          color: inherit;
          text-decoration: none;

          &:last-child {  border-bottom: none;}
          &:hover {
            background: @gray-lightest;
            //color: @white;
          }

          img {
            margin-right: 10px;
          }
        } //div

        [class^="col-"]{
          border: 1px solid @border-color;
          border-right-color: transparent;
          border-bottom-color: transparent;
          img{
            width: 100px;
          }
          &.col-md-6{
            &:nth-child(1),
            &:nth-child(2){
              border-top-color: transparent;
            }
            &:nth-child(2){
              @media (max-width: @screen-sm-max) { 
                border-top: 1px solid @border-color;
              }
            }

            &:nth-of-type(2n+1){
              border-left-color: transparent;
            }
          }
          @media (max-width: @screen-sm-max) { 
            border-left-color: transparent;
          }
        }



        &.docs-underlined {
          border-left: none;

          > div {
            padding: 10px 20px;
            //border-right: none;
            //border-top: none;
          }
          .file-icon{
            &:extend(.pull-left,.opacity-50);
            width:40px;
            > .fa {
              &:extend(.fa-2x);
            }
            .pdf {
              &:extend(.fa-file-pdf-o);
              color:#ff0000;
              }
            .word {
              &:extend(.fa-file-word-o);
              color:#0054a6;
              }
            .excel {
              &:extend(.fa-file-excel-o);
              color:#008641;
              }
          }
          .file-name {
            &:extend(.pull-left);
            a {
              font-weight: 400;
              font-size: 110%;
            }
          }
          .create {
            &:extend(.opacity-50);
          }
          .list-inline > li {
            &:extend(.opacity-50);
            display: inline-block;
            font-size:90%;
            padding: 0 5px;
          }
        }
      }

    //Поставщики extend: contacts
    .user-layer > .row {
      border:0;
    }
    .list-material > li a.visible {
      font-size: 110%; 
    } 



} //.zaya-desc




