.dataTables_scrollHead {
    background:fade(@black, 5%);
}


.offers {
  //margin-top:-50px;
  background:@white;
  table {
    table-layout: fixed;
    border-collapse: collapse;
  }
    th {
      font-size:80%;
    }
    .dataTables_info {
      font-size:80%;
    }
    tr{
      .t_idx {
        width:10px; 
        font-size:80%; 
        padding:0 0 0 5px; 
        text-align:right;
        color:@gray-light;
        }
      .t_name {
        width:200px;
        font-size:80%;
      }
      .t_suppl{}
      .highlight {
          background: rgba(0, 204, 255, 0.1)!important;
          //color: @white;
          .name-wrap:after {
            .horizontal-gradient(rgba(255, 255, 255, 0), @gray-lightest)
          }
      }
      .cell {
         color:red; 
      }
      .t_name{
        .text-overflow();
        position:relative;
          &:after {
            content:'';
            position:absolute;
            top:0;
            right:0;
            width:50px;
            height:50px;
            z-index:10;
            .horizontal-gradient(rgba(255, 255, 255, 0), #ffffff);
          }
      }
    } //.tr

    tbody tr.odd .t_name:after {
      .horizontal-gradient(rgba(255, 255, 255, 0), @table-bg-accent); 
    }
    tbody tr:hover .t_name:after, 
    tbody tr.odd:hover .t_name:after, 
    tbody tr.even:hover .t_name:after {
      .horizontal-gradient(rgba(255, 255, 255, 0), shade(@table-row-background, 3.6%)); 
      &.selected {
        background-color: shade(@table-row-selected, 3.6%);
        color: contrast(shade(@table-row-selected, 3.6%), @text-color, @white, @color-threshold);
      }
    }

    .dataTables_scrollHead .t_name:after{
      display:none
    }
    .dataTables_scrollBody {
        th {
          background:@white;
        }
    }
    .dataTables_scrollBody .table {
      margin-top: -15px;
      }



    table.dataTable tbody td {
        padding: 3px 10px;
    }



    .dataTables_scrollHead {
      //.box-shadow(@z-height-panel);
    }


    .dataTables_scrollFoot {
      //.box-shadow(@z-height-panel-inverse);
      > div {
        border-top:4px solid rgba(0, 0, 0, 0.2);
      }

      .table > tfoot > tr > th,
      .table > tfoot > tr > td {
        padding: @table-condensed-cell-padding/2;
        line-height: @line-height-base;
        vertical-align: top;
        border-top: 1px solid rgba(189, 192, 193, 0.2);
      }
      .t_suppl {
        font-weight:normal;
      }
    }

      .t_suppl .fa {
        margin:0 .5em;
      }

      #ejbeatycelledit {
        width:70px;
      }

    #postconditions {
      thead{
          display:none;
      }
      .t_name {
        width: 73%;
        text-align: right;
      }
    }


} //.offers



.popover {
  width:300px;
  .popover-content {
    dl.offers_list__suppl {
