      dd {
        &:extend(.clearfix all); // Clear the floated `dt` if an empty `dd` is present
      }
      dt {
        float: left;
        width: 100px;
        padding-top: 5px;
        
        color:@gray-light;
        font-size:80%;
        font-weight:normal;

        line-height:1.1;
        clear: left;
        text-align: right;
        //.text-overflow();
      }
       dd {
        margin-left:110px;
        margin-bottom:5px;
      }

    }
  }
}









@media (max-width: @screen-sm-min) {
  .modal .zayavka .st {
    font-size:85%;
    li {
      display:block;
      text-align:right;
    }
  }
  .zaya-desc {
    font-size:1em;
    .nav-tabs {
      position:relative;
      top:0;
      left:0;
      z-index:1200;
      > li > a {
        padding: 10px 5px;
        font-size: 85%;
      }
    }
    .tab-pane {
      margin-top:0;
      min-height:auto;
    }
    .offers {
      margin-top:0;
    }
  }
}

