/*
Контакты и чат компонент
*/
@border-color: #f0f0f0;

.list-material{
  position: relative;
  padding: 0;
  margin: 0;
  background: #fff;
  > li{
    position: relative;
    list-style-type: none;
    overflow: hidden;

    a.visible{
      text-decoration: none;
      color: @text-color;
      display: block;
      padding: 0 0 0 16px;
      transition: .3s transform cubic-bezier(.17,.67,.6,1.3);
      background: #fff;
      font-size: 130%;
    }

    a.hidden{
      display: block !important;
      position: absolute;
      top: 0;
      right: 0;

      background: @border-color;
      box-shadow: inset 2px 0px 5px rgba(0,0,0,.1);
      width: 60px;
      height: 100%;
      color: @text-color;
      transition: .3s all cubic-bezier(.17,.67,.6,1.3);

      i{
        position: absolute;
        top: 50%;
        left: 0;

        display: block;
        width: 100%;

        text-align: center;
        font-size: 22px;
        transform: translateY(-50%);
      }
    }

    .list-content{
      position: relative;
      margin-left: 0;
      margin-right: 0;
      padding: 16px 16px 16px 0;
      border-bottom: 1px solid @border-color;

      .title{
        font-weight: 400;
      }
      .caption{
        font-size: 80%;
        color: fade(@text-color, 50%);
      }

      .title,
      .caption{
        display: block;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
      }

    }

    .list-action-left{
      position: absolute;
      top: 50%;
      left: 0;
      padding: 0 16px;
      width: 72px;
      border-radius: 50%;
      transform: translateY(-50%);
      img{
        &:extend(.img-circle);
        width: 40px;
        height: 40px;
      }
      .icon{
        position: relative;
        left: 0;
        top: 2px;
        width: 1em;
        font-size: 30px;
        line-height: 1;
        width: auto;
        height: auto;
        display: block;
        margin: 0;
        background-size: initial;
        text-align: center;
      }
      .icon-circle{
        display: block;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        text-align: center;
        background: @border-color;
        padding: 5px;
        font-size: 20px;
      }
    }
    .list-action-right{
      position: absolute;
      top: 10%;
      bottom: 10%;
      right: 16px;
      padding: 0;
      width: 50px;

      text-align: right;
      font-size: 70%;
      color: fade(@text-color, 40%);
      background:@white;
      .box-shadow(-15px 0 20px 0px @white);


      i{
        font-size: 20px;
      }
      .top{
        position: absolute;
        top: 19px;
        right: 0;
      }
      .center{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
      }
      .bottom{
        position: absolute;
        right: 0;
        bottom: 10px;
      }
    }

    &:last-child{
      .list-content{
        border-bottom-color: transparent;
      }
    }

    &.has-action-left{
      .list-content{
        margin-left: 55px;
      }
    }

    &.has-action-right{
      .list-content{
        padding-right: 60px;
      }
    }

    &.has-long-story{
      .list-action-left{
        top: 23px;
        transform: translateY(0);
      }
      .caption{
        white-space: inherit;
      }
    }

    &.divider{
      font-size: 14px;
      padding: 10px 16px 10px 16px;
      margin-top: 10px;
      &.subheader{
        padding-left: 72px;
      }
      &.with-border{
        border-top: 1px solid @border-color;
      }
    }

  }

  &.has-hidden{
    >li:hover{
      .hidden{
        visibility: visible !important;
      }
      a.visible{
        transform: translate3d(-60px,0,0);
      }
    }
  }

  &.borderless{
    .list-content{
      border-bottom-color: transparent;
    }
  }
}


.user-layer{
  //display: none;
  //position: fixed;
  //left: 80px;
  //top: 0;
  background: transparent;
  height: 100%;
  padding: 0;
  //visibility: hidden;
  //z-index: 1100;

  > .row {
    background: #fff;
    padding: 0px;
    height: calc(~'100% - 150px');

    border: 0;
    border-top: 1px solid @border-color;

    visibility: hidden;
    transform: scale(0);
    //border-radius: 100%;
    transition: all 300ms cubic-bezier(0.35, 0, 0.25, 1) 0;

    #messages{
      height: 100%;
    }

    .message-list{
      visibility: hidden;
      transform: scale(0);
      transition: all 300ms cubic-bezier(0.35, 0, 0.25, 1) 150ms;

      padding: 0;
      margin: 0;
      //height: 100%;

      .selected{
        .list-content{
          border-bottom-color: transparent;
        }
      }

      .list-action-left{
        //left: 20px;
        left:0;
        img{
          width: 50px;
          height: 50px;
        }
      }
      .list-content{
        margin-left: 60px;
        .title{
          font-size: inherit;
        }
        .detail2{
          color: fade(@text-color, 80%);
          border-bottom-color: @border-color;
        }
      }
      .list-action-right{
        right: 20px;
        width: 55px;
        color: fade(@text-color, 30%);
      }
    }

    .send-message{
      position: absolute;
      z-index: 1;
      right: 0;
      bottom: 0;
      border-top: 1px solid @border-color;
      width: 100%;
      padding: 20px 30px;
      background: #fff;
      textarea{
        max-height: 100px;
      }
    }
    
    .messages{
      padding: 0px 30px 0 30px;
      //height: calc(~'100% - 89px');
      height: 100%-(80px);

      position: relative;
      overflow: auto;
      -webkit-overflow-scrolling: touch;
    }

    [class^="col-"], [class*=" col-"]{
      position: relative;
      height: 100%;
    }


    #messages{
      >div:nth-child(1){
        border-right: 1px solid @border-color;
      }
      .list-material{
        //background: transparent;
        a{
          //background: transparent;
        }

        a:before {
            content:'';
            position: absolute;
            background:fade(@brand-primary, 10%);
            top: 0; bottom:0;
            left: 0; right: 0;
            opacity: 0;
            z-index: 10;
            -webkit-transform: scale(0.9);
            .transition(all 320ms ease-out);
        }

        .loaded & a:hover:before,
        .loaded & .selected a:before {
          opacity: 1;
          -webkit-transform: none
        }
        .loaded & a:active {
          .transform(scale(.95));  
        }

      }
      .message-list{
        position: relative;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
      }
      .message-send-container{
        height: 100%;

        visibility: hidden;
        transform: scale(0);
        transition: transform 300ms cubic-bezier(0.35, 0, 0.25, 1) 100ms, opacity 280ms cubic-bezier(0.35, 0, 0.25, 1);


        .loading-bar{
          position: absolute;
          top: 0;
          margin-bottom: 0;
        }

        &.loading{
          .messages, .send-message{
            opacity: .1;
            overflow: hidden !important;
          }
        }
      }

      .mobile-back{
        position: absolute;
        top: -132px;
        left: 0;
        width: 100%;
        color: #fff;
        height: 61px;
        line-height: 61px;
        font-size: 30px;
        transform: translateY(-100%);
        visibility: hidden;
        transition: all 180ms cubic-bezier(0.35, 0, 0.25, 1);

        .mobile-back-button{
          padding: 0 20px;
        }

        &.active{
          transform: translateY(0%);
          visibility: visible;
          transition: all 280ms cubic-bezier(0.35, 0, 0.25, 1) 100ms;
        }
      }
    }

  }

}


.profile-card {
  .row {
    margin-bottom:2em;
    .actions{
      &:extend(.stick-top-right,.text-success,.force-padding);
      .box-shadow(-5px 5px 20px 15px @white);
      background:@white;
      a {
        &:extend(.btn, .ink-reaction, .btn-xs);
        margin-right:1em;
        margin-bottom:1em;
        display:block;
        text-decoration:none;
        .transition(transform 320ms cubic-bezier(0.17, 0.67, 0.6, 1.3));      
        &:active {
          .transform(scale(.8));
        }  
      }
    }
  }
  .photo {
    &:extend(.pull-left, .width-3, .clearfix, .text-center);
    margin-top: 1em;

    img {
      &:extend(.img-circle, .size-2);
    }

  }


  h1 {
    &:extend(.text-light);
    font-size:185%;
  }
  h6 {
    &:extend(.text-light);
  }

  dl {
    &:extend(.dl-horizontal.dl-icon,.text-lg);
    dt span {
      &:extend(.text-medium,.opacity-25);
    }
    dd span {
      margin-right:.5em;
      &:nth-child(1) {
        &:extend(.opacity-25, .text-medium);
      }
      &:nth-child(2), a {
        &:extend(.text-medium);
      }
    }

  }  
  
}
.messages {
  .profile-card {
    margin-top:2em
  }
}



//Aнимация
  
.each-anim(message-list__users, 1s, 2s);

.loaded .user-layer{
  visibility: visible;

  .tab-content{
    visibility: visible;
    transform: scale(1);
    border-radius: 0;
    transition: all 280ms cubic-bezier(0.35, 0, 0.25, 1) 380ms;

    .message-list{
      visibility: visible;
      transform: scale(1);
      transition: all 280ms cubic-bezier(0.35, 0, 0.25, 1) 560ms;
    }
    #messages{
      .message-send-container{
        visibility: visible;
        transform: scale(1);
        transition: transform 280ms cubic-bezier(0.35, 0, 0.25, 1) 660ms, opacity 280ms cubic-bezier(0.35, 0, 0.25, 1);
      }
    }
  }

  &.perfect-scrollbar{
    .tab-content{
      #messages{
        .message-list{
          overflow: hidden !important;
        }
      }
      .messages{
        overflow: hidden !important;
      }
      #notifications,
      #settings{
        overflow: hidden !important;
      }
    }
  }

} //.loaded
