// Tab mixins
// --------------------------------------------------


// Tab variant
// -------------------------

.tab-variant(@name; @color) {
	.nav-tabs.@{name} {
		> li {
			&.active > a {
				&, &:hover, &:focus {
					&::after { transform: scale(1); background:@color; }
					border-color: @color;
					color: @color;
				}
			}
		}
	}
}
