					position:relative;

					white-space: nowrap;
					min-width: 100px;
					max-width: 150px;
					margin:10px 5px;
					//height:50px;
					overflow:hidden;

				small {
				}
					&:before{
							content:'';
							position:absolute;
							top:0;
							right:0;
							width:30px;
							height:30px;
							//background-color:red;
							.horizontal-gradient(rgba(0, 52, 77, 0), rgb(0, 52, 77, 1))
					}

			}
		}
		.dropdown.open {
			a {
				  //padding: 6px 12px 12px 12px;
			}
			.profile-info:before {
				.horizontal-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 1))
			}
		}
	}
}

// Large Devices only
// -------------------------

@media (min-width: @screen-lg-min) { 
	.menubar-first {
		&.menubar-pin {
			.header-nav {
				// Hide the header brand when the menu is both pinnen and first
				.header-nav-brand {
					display: none;
				}
			}
			// Adjust header left alignment
			#header {
				left: @menubar-width;
			}
		}
	}

.header-nav-profile {
		.dropdown {
		.profile-info {
			max-width: 200px;
		}
	}
}



