//
// Company
// --------------------------------------------------

.company-portlet {
  .company-name{  
    //height:200px;
    padding:2em 3em 0 3em;
    background-image: url(/img/header-bg.png);
    background-size: cover;
    background-color: #039be5;
    color: #ffffff;
    border-bottom: 1px solid @white;
    margin:0;
    position: relative;
    h2 {
      margin:1em 0 1em 0;
      position: relative;
      padding-right:80px;
      @media (max-width: @screen-xs-max) { 
        margin-top:0;
      }
      small {
        color: #fff;
        margin: 10px 0;
        font-size: 35%;
        display: block;
        text-transform: uppercase;
        letter-spacing: .15em;
        display:block;
          @media (max-width: @screen-xs-max) {
            display:block;
            margin-left:0;
            display:block;
            font-size: 50%;
            line-height: 1.2;
            font-weight: 100;
            margin: 10px 0;
          }
      }
    }
    @media (max-width: @screen-xs-max) {
      height:inherit;
        h2 {
          font-size:140%;
        } 
        h3 {
          font-size:120%;
        }
    } // / xs-max .company-name
  } // .company-name

  .company-badges {
    list-style:none;
    position: absolute;
    right:0;
    top:-31px;
    overflow: hidden;
    @media (max-width: @screen-xs-max) {
      display:none;
    }
    li{
      float:left;
      position: relative;
      padding:5px 10px;
      margin:0 20px 0 0;
      background: #ffe676;
      color:#cc5e14;
      font-weight: bold;
      font-size:80%;
      text-transform: uppercase;
      &:after {
        content:'';
        position: absolute;
        left: 0;
        top:0px;
        left:-8px;
        width:0;
        border:4px solid;
            border-top-color: transparent;
        border-right-color:#dead38;
        border-bottom-color:#dead38;
        border-left-color: transparent;
      }
      a, a:hover {
        i {padding-right:5px;font-size: 120%;}
        text-decoration:none
      }

    }
  }
  //COMPANY_MENU
  .company-mmenu {
    list-style:none;
    position: absolute;
    right:0;
    top:0px;
    margin:0;
    padding: 0;
    li{
      float:left;
      margin-right:20px; 

        @media (max-width: @screen-xs-max) { 
            float:none;
            margin:0 5px 0 0;
          }

      padding:0;
      width: 30px;
      height: 30px;
      line-height: 28px;
      text-align: center;
      a, a:hover {
        i {padding-right:0;}
        color:@brand-primary-bright;
        text-decoration:none;
        //font-size:90%; 

      }

    }
  } // /.company-mmenu

 .nav-company {
    //font-family: 'Roboto Condensed', sans-serif;
    //font-weight: bold;
    //letter-spacing:-.05em;

    font-size:1.3em;
    text-transform: uppercase;
    //margin-top: -30px;
    background: @white;
    float:left;
    margin-left:33%;
      @media (max-width: @screen-xs-max) {
        margin-top:0;
        margin-left:0;
      }

    a {
      color: @text-color;

      @media (max-width: @screen-xs-max) {
        padding:8px 6px;
        font-size:80%;
      }

      span {
        font-size: 130%;
        line-height: 1.5em;
      }
    }
 } // .nav-company


  //CONTENT BLOCK 
  .company-body{
    padding-top:30px;
    dl.dl-horizontal {
      @media (max-width: @screen-xs-max) { 
        font-size:95%;
      }
      dt {
        width:135px
      }
      dd {
        margin-left:145px;
      }
    }

    //LEFT BLOCK    
    .menubar {
      h3 {
        margin-top:0;
      }
      //.nav-catalog
      .nav-catalog {
        //&:extend(.nav,.nav-pills,.nav-stacked,.nav-transparent,.nav-nowrap);
        margin-bottom:1em;
        font-size:120%;
        > li {
          border-bottom: 1px solid rgba(0, 0, 0, 0.05);
          &:last-child {
            border:0;
          }
        }
        > li > a {
          overflow: hidden;
          height:40px;
          white-space: nowrap;
          position: relative;
          border:0;
          .badge {
            position: absolute;
            right:15px;
            top:12px;
            .box-shadow(0 0 40px 35px #ffffff);
          }
          &:hover .badge,
          &:active .badge{
            .box-shadow(0 0 40px 35px @nav-link-hover-bg);
          }
        } // a
        > li.active > a {
          color: inherit;
          background: transparent;
        }
      } //.nav-catalog
      
      //Accordion
      .panel {
        border:0;
        .panel-heading {
          cursor: pointer; cursor: hand;
        }
        .panel-body {
          padding:0;
          .nav-catalog {
            font-size:90%;
            margin-bottom:0;
          }
        }
        .nav-catalog {
          font-size:100%;
        }
        .tools {
          padding:0;
        }
        .btn-icon-toggle {
          padding: 0;
          width: 15px;
            .fa {
              line-height:15px;
            }
            .fa-plus {
              .rotate(-135deg);
              //@mixin rotate(-135deg);
              .transition(all 0.3s ease-out);
              //@mixin transition(all 0.3s ease-out);
            }
          }
        .collapsed{
          .fa-plus {
            .rotate(0);
            //@mixin rotate(0);
            .transition(all 0.3s ease-out);
            //@mixin transition(all 0.3s ease-out);
          }
        }  
      }//.panel


      .header {
        a:hover {
          color:darken(rgb(red(@brand-primary), green(@brand-primary), blue(@brand-primary) + 15), 25);
        }
      }
      .card-head.card-head-xs header {
        padding:0;
      }
    } // /.menubar
  //КОНТАКТЫ
  .company-users {
    &:extend(.list-results);
      @media (max-width: @screen-md-max) { 
        //&:extend(.btn-xs);
      }

    .contact {
      //position:relative;
      
      .photo {
         width:60px; //80px
        img {
          &:extend(.img-circle, .img-responsive, .pull-left);
        }
        @media (max-width: @screen-md-max) { 
          width:80px;
        }
      } // /.photo
      .info {
        &:extend(.v-top);

        .clearfix > [class^="col-"] {
          white-space:nowrap;
        }
        span {
          &:extend(.text-sm);
        }

      } // /.info

    } // /.contact

    .btn {
      &:extend(.ink-reaction, .btn-xs, .btn-primary-bright);
        &:nth-child(2) {
          &:extend(.btn-accent-bright);        
        }
      margin:.5em;
      margin-left:0;
    } // /.btn
    .status {
      &:extend(.stick-top-right, .force-padding);
      background:@white;
      .box-shadow(-13px 17px 25px 2px @white);
        &.online {
          &:extend(.text-success);
        }
        &.offline {
          &:extend(.text-danger);
        }
    } // /.status

    .contact:hover .status {
      background:@gray-lightest;
      .box-shadow(-13px 17px 25px 2px @gray-lightest);
    }
  } // /.company-users

  }// /.company-content #RIGHT BLOCK
}// /.company-portlet

  //RIGHT BLOCK
.company-content,
.searchres-content {
  h2 {
    margin-top:0;
    small {
        color: #fff;
        margin: 10px;
        font-size: 50%;
    }
  } 

  p {
    font-size: floor((@font-size-base * 1.05));
    &.lead {
     font-size: floor((@font-size-base * 1.25));
      }
  }
  dl {
    font-size: floor((@font-size-base * 1.15));
    dd,dt {
      margin-bottom:.25em;
    }
  }      

  .catalog-filter {
    .panel {
      .panel-heading .panel-title {
        font-size:80%;
      }
    }

  }

  .catalog-actions {
    margin-bottom: 20px;
    
    &.well,
    .row {
      padding: 0;
      border:none;
      background: transparent;
      .box-shadow(none);
      //@include box-shadow(none);
    }
    .btn-group {
      margin:0 0 0 0;
    }
    .glyphicon {
      margin-right:5px;
    }

    .btn {
      &:extend(.ink-reaction, .btn-sm, .btn-default-light);
        @media (max-width: @screen-md-max) { 
          &:extend(.btn-xs);
        }
      .transition(transform 320ms cubic-bezier(0.17, 0.67, 0.6, 1.3));      
      &:active {
        .transform(scale(.9));
      } 
    }

  }
  .catalog-items {
    .item-thumbnail {
      margin-bottom: 20px;
      padding: 15px;
      min-height:250px;
      position:relative;
      .border-radius(0);
      border:1px solid rgba(0,0,0,0.03);
      .transition(all .15s linear);
      //@include border-radius(0);
      &:after {
        content: '';
        position: absolute;
        top: -10px;
        left: -10px;
        right: -10px;
        bottom: 0px;
        z-index:-1;

        //background: rgba(0, 0, 0, 0.05);
        border:3px solid rgba(0, 0, 0, 0.1);

        .opacity(0);
        .transform(scale(0.9));
        .transition(all 320ms ease-out);
        //.box-shadow(0 1px 3px 0 rgba(0, 0, 0, 0.33))
      }
      &:hover {
        .box-shadow(0 1px 40px 0 rgba(0,0,0,.1));
      }
      &:hover:after {
        //.opacity(1);
        .transform(none);
        
      }
    }
    .item-pic {
      height: 110px;
      overflow: hidden;
      text-align: center;
      margin-bottom: 1em;
      position: relative;
      img {
        &:extend(.img-responsive);
        width: 100%;
      }
      a {
        top:0;
        left:0;
        bottom:0;
        right:0;
        width:100%;
        height:100%;
        display:block;
        position: absolute;
        &:before {
          content:' ';
          position: absolute;
          display: block;
          width:100%;
          height:100%;
          .box-shadow(inset 0 0 50px rgba(0, 0, 0, 0.1));
        }
      }
    }
    .item-actions {
      position: absolute;
      right: 5%;
      top: 130px;
      z-index: 99;
      opacity: 0;
      .transition(all 320ms ease-out);
      
      > .btn {
        &:extend(.btn-sm);
        font-size: 150%;
        line-height: 10px;
        padding-top: 0;
        padding-bottom: 0;
        margin-right:.25em;
        .button-variant(@btn-danger-color; @btn-danger-bg; @btn-danger-border);
        .box-shadow(@z-height-0);
      }

    }
    .item-thumbnail:hover .item-actions {
      opacity: 1;
      .translate(0, -20px);
    }

    .item-heading{
      margin: 0 0 0;
      position: relative;
      height:16px;
      overflow: hidden;
      a {
        color:@black;
        text-decoration:none;
      }
      &:before {
        content:'';
        position: absolute;
        right:0;
        bottom:0px;
        width:1px;
        height:16px;
        background:@white;
        .box-shadow(-5px 5px 30px 20px @white);
      }
    }
    .item-text {
      margin: 0 0 11px;
      display:none;
    }
    .item-price {
        font-size: floor((@font-size-base * 1.5))!important;
        color:@gray-light;
        font-weight: 800;
        span {
          font-size: floor((@font-size-base * 1.05));
          margin-left:-.25em;
        }
    }
    .item .btn {
        &:extend(.ink-reaction, .btn-sm, .btn-primary);
          @media (max-width: @screen-md-max) { 
            &:extend(.btn-xs);
          }
        //position:absolute;
        //bottom:@card-gutter-width;
        //left:0;
      .transition(transform 320ms cubic-bezier(0.17, 0.67, 0.6, 1.3));      
      &:active {
        .transform(scale(.9));
      } 
    }

    //РЕЖИМ ПРОСМОРТР -- СПИСОК
    .item.list-group-item { 
      background-color: #ffffff;
      float: none;
      margin-bottom: 0;
      width: 100%;
      border:0;
      padding:20px;
      border-bottom:1px solid @gray-lighter;
      
      &:nth-of-type(odd) {
        //background:#eeeeee;
        
      }
      &:hover {
        //background: #428bca;
      }
      .list-group-image {
        margin:0 15px 15px 0;
      }
      .item-thumbnail {
        margin-bottom: 0px;
        border-color:@white;
        min-height: 150px;
          @media (max-width: @screen-sm-max) { 
            min-height:inherit;
          }
      }
      .item-pic {
        float:left;
        width:25%;
        margin-right:2em
      }
      .caption {
        padding: 9px 9px 0px 9px;
      }
      &:before {
        content: " ";
        display: table;
      }
      &:after {
        clear: both;
        content: " ";
        display: table;
      }
    } // /.list-group-item
  } // /.catalog-items
} //.company-content

.ecommerceItem {
  .photos {
  }
  .fotorama_wrap {
    height:400px;
    padding:1em;
    overflow:hidden;
  }

  .fotorama .fotorama__nav--thumbs .fotorama__nav__frame{
    padding:2px;
    height:64px
  }
  .fotorama .fotorama__thumb-border{
    height:60px;
    border-width:2px;
    margin-top:2px
  }
  .fotorama__nav--thumbs .fotorama__nav__frame {
    //padding: 0 !important;
  }
  .fotorama__arr.fotorama__arr--prev:hover,
  .fotorama__arr.fotorama__arr--next:hover {
    opacity: 1;
  }
  .fotorama__arr.fotorama__arr--prev:before {
    content: "\f104";
  }
  .fotorama__arr.fotorama__arr--next:before {
    content: "\f105";
  }
  .fotorama__arr.fotorama__arr--prev,
  .fotorama__arr.fotorama__arr--next {
    background: none;
    .border-radius(3px);
    color: #fff;
    font: normal normal normal 2em/1 FontAwesome;
    text-align: center;
    width: 42px;
    height: 42px;
    padding: 10px;
    //opacity: .33;;
    text-shadow: 0px 0px 5px rgba(0, 0, 0, .3);
  }
  .fotorama__fullscreen-icon {
    background:none;
    &:before {
      content:"\f002";
      font: normal normal normal 20px/1 FontAwesome;
      text-shadow: 0px 0px 5px rgba(255, 255, 255, .3);
    }
  }

  .head h3 {
    margin-bottom:.25em;
    max-height: 175px;
    overflow: hidden;
  }
  .price {
    font-size:350%;
    letter-spacing: -.05em;
    font-weight: 800;
    margin-top: .25em;
    margin-bottom:0;
    line-height:1.2;
    span {
      font-size: 60%;
      padding: 0 .25em;
    }
  }
  .avail,
  .sku {
    &:extend(.opacity-50);
    &:extend(.label);
    &:extend(.style-gray-light);
    font-weight:normal;
    font-size:90%;
    //display:block;
  }
  .sku {
    color:@black;
    background: #eee;
  }
  .buy-btns {
    text-align:center;
    .btn {
      font-size:1.3em;
      margin:0 1em 1em 0;
    }
  }
  .subbuy-btns {
    display:none;
    .btn {
      margin:0 1em 1em 0;
    }
  }
  .item-desc .card {
    font-size:120%;
  }
} // /.ecommerceItem




.loading #products {
  .opacity(.1);
}


//АДАПТИВ

@media (max-width: @screen-xs-max) {
  .modal {
    .company_user {
      .width-3 {
        width:60px;
      }   
      img.size-2 {
        width:40px;
        height: 40px;
      }
      h1 {
        font-size:160%;
      }
      h4 {
        font-size: 100%;
        margin: .5em 0;
      }
      .stick-top-right.force-padding.text-success {
        position:relative;
        padding:.5em 0;
        float: right;
      }
      dl dt {
        display:none;
      }
    }
  } // /.modal
}


