 .modal-open {
  overflow: hidden;
 }


 /* add a scroll bar to stop page from jerking around */
 .modal-open.page-overflow .page-container,
 .modal-open.page-overflow .page-container .navbar-fixed-top,
 .modal-open.page-overflow .page-container .navbar-fixed-bottom,
 .modal-open.page-overflow .modal-scrollable {
  overflow-y: scroll;
 }

 @media (max-width: 979px) {
  .modal-open.page-overflow .page-container .navbar-fixed-top,
  .modal-open.page-overflow .page-container .navbar-fixed-bottom  {
    overflow-y: visible;
  }
 }


 .modal-scrollable {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
 }

 .modal {
  outline: none;
  position: absolute;
  margin-top: 0;
  top: 50%;
  overflow: visible; /* allow content to popup out (i.e tooltips) */
 }

 .modal.fade {
  top: -100%;
 }

 .modal.fade.in {
  top: 50%;
 }

 .modal-body {
  max-height: none;
  overflow: visible;
          padding:@modal-inner-padding/2;
        @media (min-width: @screen-sm-min) {
            padding: @modal-inner-padding;
        }
 }

 .modal.modal-absolute {
  position: absolute;
  z-index: 950;
 }

 .modal .loading-mask {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: green;
  .opacity(@modal-backdrop-opacity);
  border-radius: 0;
  z-index: 999;
  &.in { .opacity(@modal-backdrop-opacity); }
 }

 .modal-backdrop.modal-absolute{
  position: absolute;
  z-index: 940;
 }

// Modal background
.modal-backdrop {
  background-color: @modal-backdrop-bg;
  // Fade for backdrop
  &.fade { .opacity(0); }
  &.in { .opacity(@modal-backdrop-opacity); }
}

.modal-content {
  box-shadow:none;
  border:0;
}


.modal.container {
  width: 940px;
  margin-left: -470px;
}

/* Modal Overflow */

.modal-overflow.modal {
  top: 1%;
  &.fade {
    top: -100%;
    &.in {
      top: 1%;
    }
  }
}

.modal-overflow .modal-body {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}






.modal-header {
  position:relative;
  height:@modal-header-height;
  line-height:@modal-header-height;
    @media (max-width: @screen-xs-max) {
      height:@modal-header-height/1.25;
      line-height:@modal-header-height/1.25;
      }
    h1,
    h2,
    h3,
    header {
      font-size: 160%;
      height:@modal-header-height;
      line-height:@modal-header-height;
      padding:0 @modal-inner-padding;
       @media (max-width: @screen-xs-max) {
          height:@modal-header-height/1.25;
          line-height:@modal-header-height/1.25;
          font-size:110%;
          padding:0 @modal-inner-padding/1.25;

        }
     }
    .btn {
      //float: right;
      position: absolute;
      right:0px;
      height:@modal-header-height;
      line-height:@modal-header-height;
      font-size:300%;
        @media (max-width: @screen-xs-max) {
          height:@modal-header-height/1.25;
          line-height:@modal-header-height/1.25;
           font-size:200%;
        } 
        i {
          height:@modal-header-height;
          line-height:@modal-header-height;
           @media (max-width: @screen-xs-max) {
              height:@modal-header-height/1.25;
              line-height:@modal-header-height/1.25;
            }
        }
    &.btn {
      .transition(transform 320ms cubic-bezier(0.17, 0.67, 0.6, 1.3));      
    }
    &.btn:active {
      .transform(scale(.8));
    }

    &.btn-actions {
      right: @modal-header-height;
      font-size: 240%;
        @media (max-width: @screen-xs-max) {
          font-size:140%;
        } 
    }

    }
    .nav-tabs {
      font-size: 1.3em;
      line-height: @modal-header-height+2;
      @media (max-width: @screen-xs-max) {
        height:@modal-header-height/1.25;
        line-height:@modal-header-height/1.25+2;
      }
      > li > a {
      padding:10px;
      }
    }//.nav-tabs
}// .modal-header

