/* Responsive */
@media (min-width: @screen-md-min) {
  .modal.container {
    width:@screen-md-min - 30;
    margin-left: (-(@screen-md-min - 30) / 2);
  }
}



@media (min-width: @screen-lg-min) {
  .modal.container {
    width:@screen-lg-min - 30;
    margin-left: (-(@screen-lg-min - 30) / 2);
  }
}


@media (min-width: 1500px) {
  .modal.container {
    width: 1470px;
    margin-left: -735px;
  }
}


@media (max-width: 979px) {
  .modal, 
  .modal.container,
  .modal.modal-overflow   {
    top: 1%;
    right: 1%;
    left: 1%;
    bottom: auto;
    width: auto !important;
    height: auto !important;
    margin: 0 !important;
    padding: 0 !important;
  }

  .modal.fade.in, 
  .modal.container.fade.in,
  .modal.modal-overflow.fade.in {
    top: 1%;
    bottom: auto;
  }

  .modal-body,
  .modal-overflow .modal-body {
    position: static;
    margin: 0;
    height: auto !important;
    max-height: none !important;
    overflow: visible !important;
  }

  .modal-footer,
  .modal-overflow .modal-footer {
    position: static;
  }
}

