//
// Icons
// --------------------------------------------------

// Common
// -------------------------

.fa.icon-tilt,
.md.icon-tilt,
.glyphicon.icon-tilt,
.icon-tilt {			
	.rotate(10deg); 
}

.fa.icon-tilt-alt,
.md.icon-tilt-alt,
.glyphicon.icon-tilt-alt,
.icon-tilt-alt {		
	.rotate(350deg); 
}


// Button icons
// -------------------------

.btn {
	&+.dropdown-toggle {
		.fa, .md {
			margin: 0;
		}
	}
	
	.fa + .fa,
	.md + .md {
		margin-right: 0;
		margin-left: 6px;
	}
}

.btn-icon-toggle {
	.fa,
	.md,
	.glyphicon {
		margin: 0;
	}
}


// Nav icons
// -------------------------

.nav a {
	&+.dropdown-toggle {
		.fa,
		.md {
			margin: 0;
		}
	}
	
	.fa + .fa,
	.md + .md {
		margin-right: 0;
		margin-left: 6px;
	}
}


// Material icons
// -------------------------

.gui-icon [class*=" md-"], [class^=md-]
,.gui-icon [class*=" fa-"], [class^=fa-]
 {
	font-size: 1.6em;
	margin-top: -0.2em;
	margin-bottom: -0.2em;
}
#header .header-nav [class*=" fa-"], [class^=fa-]
 {
	font-size: 1.6em;
}

#header .header-nav [class*=" gsnab"], [class^=gsnab-]
 {
	font-size: 2.6em;
	line-height: 49px;
}


body  {
	font-size: 1.2em;
	margin-top: -0.2em;
	margin-bottom: -0.2em;
}