//
// Navs Навигация
// --------------------------------------------------


// Nav Stacked
// --------------------------------------------------

.nav-stacked {
	border-bottom: none;
	> li {
		margin-bottom: 0;
		+ li {
			margin-top: 1px;
			margin-bottom: 0;
		}
		> a {
			margin-right: 0;
		}
	}

	&.nav-tabs > li.active > a, 
	&.nav-tabs > li.active > a:hover, 
		&.nav-tabs > li.active > a:focus {
		border: none;
	}
}


// Nav one-row
// --------------------------------------------------
.nav-nowrap {
	> li > a {
		overflow: hidden;
    height:40px;
    white-space: nowrap;
    position: relative;
		.badge {
			position: absolute;
			right:15px;
			top:12px;
		}
	}
}

// Small
// --------------------------------------------------

.nav {
	> li > small {
		display: block;
		margin: 20px 0 10px 10px;
		font-size: 10px;
		font-weight: @weight-medium;
		opacity: .6;
	}
	> li:first-child > small {
		margin-top: 0;
	}
}


// Nav icon
// --------------------------------------------------

.nav.nav-icon {
	> li {
		a {
			padding-left: 32px;
			line-height:1.3em;

			.fa,
			.md,
			.glyphicon {
				position: relative;
				width: 32px;
				text-align: center;
				margin-left: -32px;
				float: left;
				font-size: 1em;
			}
		}
	}
}


// Colors
// --------------------------------------------------

.nav-transparent {
	a {			color: inherit; border-bottom: 1px solid rgba(0, 0, 0, 0.05); }
	a:hover {	color: @text-color; }
}


// Nav pills
// --------------------------------------------------

.nav-pills {
	img {
		margin-right: 10px;
	}

	li.focus > a {
		&, &:hover, &:focus {
			background-color: @gray-lightest;
		}
	}
}


// Nav search
// --------------------------------------------------

.navbar-search {
	position: relative;
	display: inline-table;
	padding:0;
	white-space: nowrap;
	border-radius: 2px;
	.transition(background 0.25s ease-in-out);

	.form-group {
		display: table-cell;
		margin-bottom: 0;
		max-width: 0;
		vertical-align: middle;
		overflow: hidden;
		.transition(max-width 0.25s ease-in-out);
	}
	input {
		display: inline-block;
		margin:0;
		padding: 0 0 0 10px;
		max-width: 0;
		height: 36px;
		border: none;
		.transition(max-width 0.25s ease-in-out);
	}
	.btn {
		display: table-cell;
	}

	&.expanded {
		background-color: @white;

		.box-shadow(@z-height-1);

		.form-group {
			max-width: 800px;
		}
		.btn {
			color: @black;
			background: inherit;
		}
		input {
			max-width: 800px;
		}
	}
}
header .navbar-search {
		&.expanded {
			//width: 900px;	
			margin-top:5px;

			.form-group {
				width: 100%;
			}
			input {
				width: 100%;
			}

		}
	.btn-icon-toggle {
		width:40px;
	}

	.btn-icon-toggle .fa {
		line-height:39px;		
	}

}


// Responsive classes
// --------------------------------------------------

// Small Devices
// -------------------------

@media (max-width: @screen-xs-max) {
	header .navbar-search.expanded {
			width: 100%;	
		& input {
			font-size:1em;
		}
	}
}


// From Medium Devices and up
// -------------------------

@media (min-width: @screen-sm-min) { // >= 768px (small tablet)
	header .navbar-search.expanded {
			width: 250px;	
		& input {
			font-size:.9em;
			
		}
	}

}

@media (min-width: @screen-md-min) { // >= 992px (medium laptop)
	header .navbar-search.expanded {
			width: 410px;	
		& input {
			font-size:1.1em;
		}
			.form-group {
				width: 1220px;
			}

	}

}

// Large Devices
// -------------------------
@media (min-width: @screen-lg-min) { // >= 1200px (large desktop)
	header .navbar-search.expanded {
			width: 610px;	
	}
}

@media (min-width: 1600px) { // >= 1200px (large desktop)
	header .navbar-search.expanded {
			width: 1000px;	
		& input {
			font-size:1.3em;
		}
	}
}








// Utils
// --------------------------------------------------

.nav {
	li {
		&.padded {
			padding: 10px;
		}
	}
}