.dashboard {
	.card-head header {
		padding-left:10px;
	}
	.card-body {
		//padding-bottom:50px;
	}

	section .btn-icon-toggle {
		padding: 0;
		width: @menubar-icon-width/2;
		border-radius:100%;
		.fa, .md {
			line-height: @menubar-icon-width/2 - 2 ;			
		}
		&:hover {
			
		}
	}
	//company
	.company-card {
		dl {
			dt, dd {
				margin-bottom:10px;
				line-height:1.2;
				font-size: 110%; 
			}
			dt {
				width:60px
			}
			dd {
				margin-left: 80px;
			}
		}
	}
	//users
	.user-card {
		.list .tile .tile-icon {
			min-width: @menubar-icon-width/1.25 + 10;
			width: @menubar-icon-width/1.25 + 10;	
			img {
				width: @menubar-icon-width/1.25;
				height: @menubar-icon-width/1.25;
				border-radius: @menubar-icon-width/1.25;
			}
		}
	}
	//zaya
	.zaya-card {
		margin:0 20px 0 0;
		padding-right:5px;
		span i {
				//margin:5px;
			}
			.progress-bar {
				width:20%;
			}
			.pull-left {
				margin-right:10px		
			}
			.zaya-name {
				line-height: 1.4;
				height:34px;
				overflow: hidden;
				position: relative;
				&:before{
					content:'';
					position:absolute;
					bottom:0;
					right:0;
					width:115px;
					height:15px;
					//background-color:red;
					.horizontal-gradient(rgba(255, 255, 255, 0), rgb(255, 255, 255, 1))
				}
			}	
		}
	//activity-card 
	.activity-card  {
		.timeline {
			padding:20px 0 0 10px
		}
		.timeline.collapse-lg .timeline-circ{
			border-width:0px; 
			width:25px;
			height:25px;
			left:8px;
			span{
				color:#fff;
				font-size:100%;
				width:21px;
				height: 25px;
				line-height: 25px;
			}
		}
		.timeline.collapse-lg:before {
			left:20px
		}
		.timeline.collapse-lg .timeline-entry .card {
			margin-left:30px !important;
			margin-right:20px;
		}
		.timeline.collapse-lg .timeline-entry .card:after {
			display:none;	
		}

	}
}




.card .nano.has-scrollbar {
	> .nano-pane {
		z-index:10;
	}
	&:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 30px;
    width: 100%;
    background: 100px;
    box-shadow: 0 -15px 25px @white inset;
    z-index:0;
    overflow: visible;
			z-index:9;
	}
}

