//
// Progress bars
// --------------------------------------------------

// Bar hairline
// -------------------------

// Outer container
.progress-hairline {
	margin-top: 8px;
	height: 2px;
}


// Variations
// -------------------------

.progress-bar-default-dark {	.progress-bar-variant(@brand-default-dark); }
.progress-bar-default {			.progress-bar-variant(@brand-default); }
.progress-bar-default-light{	.progress-bar-variant(@brand-default-light); }
.progress-bar-default-bright{	.progress-bar-variant(@brand-default-bright); }

.progress-bar-primary-dark {	.progress-bar-variant(@brand-primary); }
.progress-bar-primary {			.progress-bar-variant(@brand-primary-light); }
.progress-bar-primary-light {	.progress-bar-variant(@brand-primary-dark); }
.progress-bar-primary-bright {	.progress-bar-variant(@brand-primary-bright); }

.progress-bar-accent-dark {		.progress-bar-variant(@brand-accent); }
.progress-bar-accent {			.progress-bar-variant(@brand-accent-light); }
.progress-bar-accent-light {	.progress-bar-variant(@brand-accent-dark); }
.progress-bar-accent-bright {	.progress-bar-variant(@brand-accent-bright); }

.progress-bar-success {			.progress-bar-variant(@brand-success); }
.progress-bar-warning {			.progress-bar-variant(@brand-warning); }
.progress-bar-danger {			.progress-bar-variant(@brand-danger); }
.progress-bar-info {			.progress-bar-variant(@brand-info); }




